.news-inner {
  .carousel-container {
    margin-bottom: calculateRem(80px);
    display: flex;
    height: calculateRem(600px);
    position: relative;
    overflow: hidden;
  }

  .flickity-button {
    right: 0 !important;
    bottom: 0 !important;
    top: auto;
    transform: unset;
    @include size(calculateRem(120px));
    background-color: $white;
    border-radius: 0;
    background-image: url("../images/svg-icons/arrow-right2.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: calculateRem(24px);
    @extend %transition;

    &:hover, &:focus, &:active {
      background-color: #F9F9F9;
      opacity: 1;
      box-shadow: none;
      outline: none;
    }

    &.flickity-prev-next-button {
      &.previous {
        left: auto;
        right: calculateRem(120px) !important;
        transform: rotate(180deg);
      }
    }

    .flickity-button-icon {
      display: none;
    }
  }

  .carousel {
    background: #FAFAFA;
    flex-grow: 1;
    position: relative;

    img {
      @include size(100%);
      object-fit: cover;
    }
  }

  .flickity-viewport {
    height: 100% !important;
  }

  .carousel-cell {
    width: 100%;
    height: 100%;
  }


  .carousel-nav {
    width: calculateRem(130px);
    max-height: 100%;
    height: 100%;
    overflow-y: auto;
    @extend %hide-scroll;
  }

  .carousel-nav .carousel-cell {
    height: calc(100% / 6);
    width: 100%;
    cursor: pointer;
    position: relative;

    img {
      @include size(100%);
      object-fit: cover;
    }
  }

  .carousel-nav .carousel-cell:before {
    font-size: 50px;
    line-height: 80px;
  }

  .carousel-nav .carousel-cell.is-nav-selected {
    &:before {
      content: '';
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
      position: absolute;
      @include size(100%)
    }
  }

  @include media("<=phone") {
    .flickity-button {
      display: none;
    }

    .carousel {
      height: 100%;
    }

    .carousel-container {
      flex-direction: column-reverse;
      height: 310px;
      margin-bottom: 40px;
    }

    .carousel-nav {
      display: flex;
      height: 64px;
      max-width: 100%;
      width: 100%;
      overflow-x: auto;

      .carousel-cell {
        min-width: calc(100% / 5);
        height: 100%;
      }
    }
  }
}
