.contact {
  margin-top: calculateRem(50px);
  //height: calc(100vh - 22.9rem);

  .container-fluid, .row {
    height: 100%;
  }

  .pages-head {
    .pages-pattern {
      bottom: auto;
    }
  }

  &__info {
    margin: calculateRem(100px) 0 calculateRem(60px);

    li {
      font-weight: 400;
      font-size: calculateRem(15px);
      line-height: calculateRem(26px);
      text-transform: uppercase;
      color: #7C7C7C;
      margin-bottom: calculateRem(45px);

      &:last-child {
        margin-bottom: 0;
      }

      a {
        margin-top: calculateRem(4px);
        font-weight: 500;
        display: block;
        font-size: calculateRem(20px);
        line-height: calculateRem(26px);
        color: $black;
        text-decoration: underline !important;
        text-underline-offset: 3px;
        text-transform: none;
        width: max-content;
      }
    }
  }

  &__social {
    display: flex;
    width: max-content;
    margin-bottom: calculateRem(70px);

    li {
      margin-left: -1px;

      &:first-child {
        margin-left: 0;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        border: 1px solid #C7C7C7;
        color: $black;
        padding: calculateRem(33px);
        position: relative;


        &:hover {
          border-color: $black;
          z-index: 2;
        }
      }
    }
  }

  &__map {
    position: relative;
    height: 100%;

    #map {
      height: 100%;
    }

    &:before {
      content: '';
      @include size(100%);
      position: absolute;
      pointer-events: none;
      background: linear-gradient(90deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 50%), linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 18.43%);
      z-index: 1;
    }
  }

  &__btn {
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 0;
  }

  @include media("<=wrapper") {
    height: auto;
  }

  @include media("<=tablet") {
    margin-top: 0;
    .row {
      flex-direction: column;
    }
    &__info {
      margin-top: 40px;
      margin-bottom: 32px;

      li {
        margin-bottom: 24px;
      }
    }

    &__social {
      margin-bottom: 24px;
    }

    &__map {
      margin: 0 -20px;

      #map {
        height: 400px;
      }

      &:before {
        display: none;
      }

      iframe {
        height: 330px;
      }

      .btn {
        position: unset;
        width: 100%;
      }
    }
  }
}