.header {
  padding: 0 0 calculateRem(44px);
  position: relative;
  $this: &;

  &__container {
    padding: calculateRem(44px) 0;
    z-index: 20;

    .container-fluid {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__logo {
    width: calculateRem(200px);

    img {
      @include size(100%);
    }
  }

  &__main-nav {
    margin: 0 auto;
  }

  &__main-menu {
    display: flex;
    align-items: center;

    li {
      margin-right: calculateRem(40px);

      &:last-child {
        margin-right: 0;
      }

      a {
        color: $black;
        font-weight: 500;
        font-size: calculateRem(20px);
        line-height: calculateRem(24px);

        &:hover {
          color: $dark-yellow !important;
        }
      }
    }
  }

  &__right {
    align-items: center;
    width: calculateRem(136px);
  }

  &__lang {
    position: absolute;
    right: calculateRem(240px);
    margin-top: calculateRem(-4px);

    a {
      font-weight: 400;
      font-size: calculateRem(20px);
      line-height: calculateRem(24px);
      color: rgba($black, .5);
      margin-right: calculateRem(32px);

      &:last-child {
        margin-right: 0;
      }

      &.active, &:hover {
        color: rgba($black, 1);
      }
    }
  }

  &__burger {
    @include size(40px, 24px);
    align-items: center;
    margin-left: auto;
    cursor: pointer;
    @extend %transition;
    margin-top: calculateRem(-8px);

    i {
      @include burger(40px, 2px, 8px);

      &:before {
        opacity: 0;
      }
    }

  }

  &__content {
    margin-top: calculateRem(140px);
  }

  &--index {
    margin-bottom: 0;
    position: absolute;
    z-index: 15;
    top: 0;
    left: 0;
    right: 0;

    #{$this}__burger {
      i {
        background-color: $white;

        &:before, &:after {
          background-color: $white;
        }
      }
    }

    #{$this}__main-menu {
      li {
        a {
          color: $white;
        }
      }
    }

    #{$this}__lang {
      a {
        color: rgba($white, .5);

        &.active, &:hover {
          color: rgba($white, 1);
        }
      }
    }

    #{$this}__logo {
      filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(2%) hue-rotate(142deg) brightness(108%) contrast(101%);
    }
  }

  @include media("<=Xwidescreen") {
    &__lang {
      right: 170px;
    }
  }

  @include media("<=laptop") {
    &__burger {
      margin-top: calculateRem(-15px);
    }
  }


  @include media("<=desktop") {
    &__lang {
      right: 100px;
      margin-top: 0;
    }

    &__burger {
      margin-top: calculateRem(-10px);
    }
  }

  @include media("<=wrapper") {
    &__container {
    }

    &__logo {
      width: 100px;
    }

    &__burger {
      @include size(32px, 24px);

      i {
        @include burger(32px, 2px, 8px);

        &:before {
          opacity: 0;
        }
      }
    }

    &__right {
      margin-top: 5px;
    }

    &__lang {
      right: 150px;
    }
  }

  @include media("<=medium") {
    padding: 0 0 20px;
    overflow: hidden;
    &.fixed {
      #{$this}__container {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
      }
    }
    &.open {
      &.header--index {
        z-index: auto;
      }

      #{$this}__burger {
      }

      #{$this}__logo {
        filter: brightness(0) saturate(100%) invert(4%) sepia(3%) saturate(3948%) hue-rotate(201deg) brightness(89%) contrast(84%);
      }

      #{$this}__lang {
        a {
          color: $black;
        }
      }
    }

    &__container {
      padding-top: 0;
      margin-bottom: 0;

      .container-fluid {
        padding-right: 0;
      }
    }

    &__content {
      margin-top: 40px;
    }

    &__burger {
      @include size(72px);
      padding-bottom: 10px;
      justify-content: center;
      margin-top: 0;

      &.active {
        background-color: $black;
        padding-bottom: 0;

        i {
          @include burger-to-cross();
          width: 20px;
          background-color: $black !important;

          &:after, &:before {
            width: 20px;
            background-color: $white;
          }

          &:before {
            opacity: 1;
          }
        }
      }
    }

    &__logo {
      width: 120px;
    }

    &__lang {
      position: unset;
      margin-right: 24px;

      a {
        display: none;
        margin-right: 0;

        &.active {
          display: block;
        }
      }
    }

    &__right {
      margin-top: 0;
      width: auto;
      display: flex;
    }

    &__main-menu {
      display: none;
    }
  }
}





