.projects-page {
  margin-top: calculateRem(35px);

  .tab {
    &__links {
      &__item {
        font-weight: 500;
        font-size: calculateRem(48px);
        line-height: calculateRem(48px);
        letter-spacing: -0.03em;
        color: rgba($black, .4);
        margin-right: calculateRem(50px);

        &:last-child {
          margin-right: 0;
        }

        &.active, &:hover {
          color: rgba($black, 1);
        }
      }
    }

    &__content {
      &__item {
        display: none;

        &.active {
          display: block;
        }
      }
    }
  }

  @include media("<=wrapper") {
    .tab {
      &__links {
        &__item {
          font-size: 28px;
          margin-right: 35px;
        }
      }
    }
  }
  @include media("<=tablet") {
    .tab {
      &__links {
        overflow-x: auto;
        display: flex;
        white-space: nowrap;
        padding-bottom: 15px;
        margin-bottom: 30px;
        &__item{
          font-size: 24px;
          line-height: 24px;
          letter-spacing: -0.02em;
        }
      }
    }

    .projects{
      padding-top: 0;
    }
  }
}