.services {
  position: relative;

  * > {
    position: relative;
    z-index: 2;
  }

  &__pattern {
    position: absolute;
    pointer-events: none;
    left: 50%;
    top: 0;
    bottom: 0;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
  }

  &__pattern2 {
    position: absolute;
    bottom: calculateRem(-20px);
    left: calculateRem(-50px);
    pointer-events: none;
  }

  .section-title {
    margin-bottom: calculateRem(130px);
  }

  &__left {
    padding-top: calculateRem(80px);
    padding-left: calculateRem(48px);
    z-index: 1;
  }

  &__right {
    background-color: #F9F9F9;
    min-height: 100vh;
    position: relative;
    z-index: 1;


    .dots {
      @include size(calculateRem(20px));
      background-color: #DDDDDD;
      position: absolute;
      left: -10px;
      top: 50%;
      transform: translateY(-50%);

      &:after, &:before {
        content: '';
        @include size(calculateRem(20px));
        background-color: #DDDDDD;
        position: absolute;
        top: calculateRem(400px);
      }

      &:after {
        top: auto;
        bottom: calculateRem(400px);
      }
    }
  }


  &__desc {
    max-width: calculateRem(504px);
    margin-left: auto;
    height: 100%;
    display: flex;
    flex-direction: column;

    span {
      font-weight: 500;
      font-size: calculateRem(16px);
      line-height: calculateRem(24px);
      letter-spacing: -0.01em;
      color: #7C7C7C;
      margin-bottom: calculateRem(4px);
      display: block;
    }

    h3 {
      font-weight: 500;
      font-size: calculateRem(28px);
      line-height: calculateRem(36px);
      letter-spacing: -0.01em;
      margin-bottom: calculateRem(52px);
    }

    p {
      font-weight: 500;
      font-size: calculateRem(16px);
      line-height: calculateRem(24px);
      margin-bottom: calculateRem(16px);

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__content {
    padding-right: calculateRem(48px);
    margin: calculateRem(250px) 0 auto;
  }

  @include media("<=laptop") {

    .section-title{
      margin-bottom: calculateRem(150px);
    }

    .section-menu {
      li {
        a {
          white-space: pre;
        }
      }
    }
    &__pattern {
      width: 500px;
    }

    &__pattern2 {
      width: 540px;
    }

    &__content {
      padding-left: 60px;
    }
  }

  @include media("<=wrapper") {
    &__pattern {
      width: 370px;
    }

    &__pattern2 {
      width: 400px;
      bottom: -55px;
    }

    .section-title {
      margin-bottom: 80px;
    }

    &__right {
      min-height: 685px;
    }

    &__desc {
      margin-left: 150px;
    }

    &__content {
      max-width: 250px;
      padding-left: 0;
      padding-right: 0;

      span {
        margin-bottom: 4px;
      }

      h3 {
        margin-bottom: 24px;
      }
    }
  }

  @include media("<=tablet") {
    display: none;
  }
}