.content {
  max-width: calculateRem(990px);
  position: relative;
  margin: 0 auto;
  z-index: 2;

  &__block {
    margin: calculateRem(-350px) auto 0;
  }

  .pages-head {
    margin-bottom: calculateRem(48px);

    .pages-title {
      font-weight: 500;
      font-size: calculateRem(40px);
      line-height: calculateRem(48px);
      letter-spacing: -0.04em;
    }

    ul {
      margin-top: calculateRem(32px);
    }
  }

  h3 {
    font-weight: 600;
    font-size: calculateRem(26px);
    line-height: calculateRem(34px);
    letter-spacing: -0.02em;
    margin-bottom: calculateRem(40px);
  }

  &__img {
    margin-bottom: calculateRem(40px);

    img {
      width: 100%;
    }

    span {
      font-weight: 400;
      font-size: calculateRem(20px);
      line-height: calculateRem(34px);
      color: #7C7C7C;
      margin-top: calculateRem(20px);
      display: block;
    }
  }

  p {
    font-weight: 400;
    font-size: calculateRem(20px);
    line-height: calculateRem(34px);
    margin-bottom: calculateRem(32px);
  }

  &__list {
    margin-bottom: calculateRem(48px);

    li {
      font-weight: 500;
      font-size: calculateRem(18px);
      line-height: calculateRem(26px);
      margin-bottom: calculateRem(25px);
      display: flex;

      &:last-child {
        margin-bottom: 0;
      }

      &:before {
        content: '';
        @include size(calculateRem(20px));
        min-width: calculateRem(20px);
        background-color: $yellow;
        display: block;
        margin-right: calculateRem(25px);
      }
    }
  }

  &__slider {
    display: flex;
    overflow: hidden;

    .carousel__slide {
      width: 100%;

      img {
        width: 100%;
      }
    }
  }

  @include media("<=wrapper") {

  }

  @include media("<=tablet") {
    .pages-head {
      margin-bottom: 32px;

      span {
        font-size: 14px;
        line-height: 20px;
        margin-bottom: 12px;
      }

      .pages-title {
        font-size: 20px;
        line-height: 32px;
        letter-spacing: -0.02em;
        margin-bottom: 30px;
      }

      ul {
        li {
          &:last-child {
            display: none;
          }
        }
      }
    }
  }
}