.social {
  margin: calculateRem(200px) 0 0;

  &__item {
    display: block;
    margin-bottom: calculateRem(70px);
    height: calc(100% - #{calculateRem(70px)});
    overflow: hidden;

    &:hover{
      .social__img{
        img{
          transform: scale(1.05);
        }
      }
    }
  }

  &__img {
    border: 1px solid $white;
    overflow: hidden;
    position: relative;

    img {
      @extend %transition;
      width: 100%;
      height: 100%;
    }
  }

  &__text {
    margin-top: calculateRem(40px);
  }

  &__date {
    font-size: calculateRem(20px);
    line-height: calculateRem(26px);
    color: #7C7C7C;
    margin-bottom: calculateRem(30px);
    display: block;
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(40px);
    line-height: calculateRem(48px);
    letter-spacing: -0.03em;
    max-width: calculateRem(650px);
  }

  @include media("<=wrapper") {

    &__item {
      margin-bottom: 40px;
      height: calc(100% - 40px);
    }

    &__text {
      margin-top: 25px;
    }

    &__date {
      margin-bottom: 20px;
    }
    &__title {
      font-size: 26px;
      line-height: 32px;
    }
  }
  @include media("<=tablet") {
    margin: 40px 0 0;

    &__item {
      margin-bottom: 24px;
      height: calc(100% - 24px);
    }

    &__text {
      margin-top: 16px;
    }

    &__date {
      font-size: 13px;
      line-height: 20px;
      margin-bottom: 8px;
    }

    &__title {
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.02em;
    }
  }
}