.projects-inner {
  margin-top: calculateRem(60px);

  .section-title {
    margin-bottom: calculateRem(65px);
    margin-top: calculateRem(110px);
  }

  &__desc {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    max-width: calculateRem(500px);
    margin: calculateRem(150px) auto 0;
  }

  &__img {
    border: 2px solid $white;

    img {
      width: 100%;
    }
  }

  .tab {
    &__links {
      margin-bottom: calculateRem(40px);

      &__item {
        font-weight: 500;
        font-size: calculateRem(40px);
        line-height: calculateRem(48px);
        letter-spacing: -0.03em;
        color: rgba($black, .4);
        margin-right: calculateRem(40px);

        &:last-child {
          margin-right: 0;
        }

        &.active, &:hover {
          color: rgba($black, 1);
        }
      }
    }

    &__content {
      &__item {
        display: none;

        &.active {
          display: block;
        }

        p {
          font-weight: 400;
          font-size: calculateRem(16px);
          line-height: calculateRem(26px);
          margin-bottom: calculateRem(20px);

          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }

  .next-project {
    margin: calculateRem(130px) 0;
    display: block;
    position: relative;
    overflow: hidden;

    &:hover {
      .next-project__img {
        &:after {
          opacity: 1;
        }
      }

      img {
        transform: scale(1.05);
      }
    }

    &__img {
      position: relative;

      &:before {
        content: '';
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
        @include fullscreen();
        z-index: 1;
      }

      &:after {
        content: '';
        @include fullscreen();
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
        opacity: 0;
        @extend %transition;
      }

      img {
        width: 100%;
        @extend %transition;
      }
    }

    &__btn {
      font-weight: 500;
      font-size: calculateRem(18px);
      line-height: calculateRem(26px);
      display: block;
      color: $white;
    }

    &__desc {
      text-align: center;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
      color: $white;
      z-index: 2;

      .pages-title {
        color: inherit;
        font-weight: 500;
        font-size: calculateRem(64px);
        line-height: calculateRem(68px);
        text-align: center;
        letter-spacing: -0.01em;
        display: block;
      }


      ul {
        justify-content: center;

        li {
          a {
            font-weight: 500;
            font-size: calculateRem(16px);
            line-height: calculateRem(26px);
            letter-spacing: -0.04em;
            padding: calculateRem(6px) calculateRem(18px);
            border: 1px solid $white;
            border-radius: calculateRem(100px);
            color: $white;

            &:hover {
              background-color: $white;
              color: $black;
            }
          }
        }
      }
    }
  }

  @include media("<=laptop") {
    &__desc {
      margin: calculateRem(100px) auto 0;
    }
  }
  @include media("<=wrapper") {
    &__desc {
      margin-top: 10px;
    }
  }
  @include media("<=tablet") {
    margin: 0;

    &__main-img {
      top: 0;
      left: 0;
      z-index: -1;
      height: 400px;
      margin: 0 -20px 40px;

      img {
        height: 100%;
        width: 100%;
        object-fit: cover;
      }
    }

    &__desc {
      margin: 0;
    }

    .tab {
      &__links {
        margin-bottom: 24px;

        &__item {
          font-size: 24px;
          line-height: 24px;
          letter-spacing: -0.02em;
        }
      }

      &__content {
        &__item {
          p {
            margin-bottom: 12px;
          }
        }
      }
    }

    .section-title {
      margin-top: 40px;
      margin-bottom: 24px;
    }

    .next-project {
      margin: 60px -20px 0;
      height: 330px;

      &__img {
        height: 100%;

        img {
          height: 100%;
          object-fit: cover;
        }
      }

      &__desc {
        top: 50%;
        left: 20px;
        right: 20px;
        transform: translateY(-50%);

        span {
          margin-bottom: 24px;
        }

        .pages-title {
          font-size: 32px;
          line-height: 40px;
          margin-bottom: 20px;
        }

        ul {
          margin-top: 0;

          li {
            font-size: 14px;
            line-height: 24px;
            letter-spacing: -0.02em;
            padding: 4px 16px;
          }
        }
      }
    }
  }
}