// forms
input, select {
  -webkit-appearance: none;
}

.form-notification {
  @include fontSize(14px);
  margin-bottom: calculateRem(15px);

  &--error {
    color: #dc3545;
  }

  &--success {
    color: $green;
  }
}

.form-group {
  display: flex;
  align-items: flex-end;

  label {
    color: $special-gray;
    @include fontSize(16px);
    display: flex;
    align-items: center;
    margin-bottom: 0;
  }

  .error-text {
    color: #dc3545 !important;
    display: block;
    margin-top: calculateRem(5px);
    font-size: calculateRem(13px);
  }

  .form-control {
    height: calculateRem(80px);
    border-color: #D3D3D3;
    border-radius: 0;
    background: $white;
    font-weight: 500;
    color: $black;
    @include fontSize(18px);
    line-height: calculateRem(26px);
    padding: calculateRem(28px) calculateRem(20px) calculateRem(8px);
    -webkit-appearance: none;
    position: relative;
    z-index: 1;
    @extend %transition;

    &.is-invalid {
      border-color: #dc3545 !important;
    }

    &::placeholder {
      color: rgba($black, .3);
      opacity: 0;
    }

    &:focus, &:hover {
      box-shadow: unset;
      outline: none;
      border-color: $black;
      z-index: 2;
    }

  }

  input[type="number"] {
    &::-webkit-outer-spin-button, &::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }

  select.form-control {
    background: url("../images/svg-icons/arrow-bottom.svg") right 20px top 50% no-repeat;
    background-size: 12.5px 6.75px;
    padding-right: calculateRem(40px);
  }

  textarea {
    resize: unset;
    height: auto!important;
  }

  &--file {
    label {
      cursor: pointer;
      padding: calculateRem(75px);
      width: 100%;
      border: 1px dashed #D3D3D3;
      border-top: 1px dashed transparent;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $black;
      font-size: calculateRem(18px);
      @extend %transition;

      img {
        margin-right: calculateRem(8px);
      }

      &:hover {
        border-color: $black;
      }
    }
  }

  @include media("<=tablet") {

  }
}

.custom-radio {
  background: $white;
  border: 2px solid $border-color;
  border-radius: 6px;
  padding: calculateRem(25px) calculateRem(30px);
  margin-bottom: calculateRem(24px);
  @extend %transition;

  &__label {
    @include fontSize(17px);
    font-weight: 600;
    color: $black;
    @extend %displayFlex;
    justify-content: unset;
    position: relative;
    cursor: pointer;

    svg {
      margin-right: calculateRem(12px);
    }

    &:before {
      content: '';
      position: absolute;
      right: 6px;
      top: 8px;
      @include size(12px);
      border-radius: 50%;
      background-color: transparent;
      @extend %transition;
    }

    &:after {
      content: '';
      position: absolute;
      right: 0;
      top: 2px;
      @include size(24px);
      border-radius: 50%;
      border: 2px solid $border-color;
      @extend %transition;
    }
  }

  .form-group {
    margin-bottom: 0;
    padding-top: calculateRem(25px);
    margin-top: calculateRem(25px);
    border-top: 1px solid #E5E7EF;
  }
}


