// Typography
body {
  font-family: $base-font;
  font-weight: normal;
  color: $base-text-color;
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6,
.h {
  display: block;
  font-family: $base-font;
  font-weight: bold;
  margin: 0;
  color: $black;
}

strong {
  font-weight: 600;
}

a {
  color: $blue;
  font-weight: normal;
  transition: $animation-speed ease;
  //transition-property: color, border-color;
  text-decoration: none;
  //border-bottom: 1px solid;
  border-color: $blue;

  &:hover,
  &:focus {
    color: currentColor;
  }
}

.btn {
  display: inline-flex;
  background-color: $yellow;
  color: $black;
  @extend %transition;
  font-weight: 600;
  @include fontSize(18px);
  line-height: calculateRem(24px);
  padding: calculateRem(61px);
  align-items: center;
  justify-content: space-between;
  width: max-content;
  cursor: pointer;


  @include media("<=wrapper") {
    @include fontSize(16px);
    line-height: 24px;
    padding: calculateRem(40px);
  }

  &:hover, &:focus {
    background: darken($yellow, 10%);
    color: black;
  }

  svg {
    margin-left: calculateRem(15px);
  }

  &--small {
    @include fontSize(14px);
    padding: calculateRem(10px) calculateRem(16px);
  }
}

.link {
  display: inline-block;
  color: $green;
  font-weight: 500;
  padding-bottom: 2px;
  @include fontSize(16px);

  svg {
    margin-left: calculateRem(13px);
  }

  &:hover {

  }

  @include media("<=tablet") {
    font-size: 14px;
    svg {
      margin-left: 13px;
    }
  }

}

@each $name, $color in $colors {
  .btn-#{$name} {
    color: $white;
    background: $color;

    &:hover, &:focus {
      background: $white;
      color: $color;
      border-color: currentColor;
    }
  }

  .btn-#{$name}-transparent {
    color: $color;
    background: transparent;
    border-color: $color;

    &:hover, &:focus {
      border-color: $color;
      background: $color;
      color: $white;
    }
  }

}

.text-center {
  text-align: center !important;
  justify-content: center;
}


.btn-full {
  padding: calculateRem(200px);
  font-weight: 500;
  font-size: calculateRem(48px);
  line-height: calculateRem(48px);
  letter-spacing: -0.04em;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  @include media("<=wrapper") {
    font-size: 32px;
    line-height: 32px;
    padding: 150px;
  }
  @include media("<=tablet") {
    padding: 68px;
    font-size: 16px;
    line-height: 24px;
  }
}

.btn-icon {
  svg {
    margin-left: 0;
  }
}

.btn-medium {
  width: calculateRem(500px);
  @include media('<=wrapper') {
    width: 300px;
  }
  @include media("<=tablet") {
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.015em;
  }
}

.border-right-0 {
  border-right: 0 !important;
}
