.statistics {
  padding: calculateRem(80px) 0 0;

  .section-title {
    padding-left: calculateRem(48px);
  }

  &__text {
    max-width: calculateRem(760px);

    p {
      font-weight: 500;
      font-size: calculateRem(48px);
      line-height: calculateRem(56px);
      letter-spacing: -0.04em;
      color: rgba($black, .5);

      strong {
        font-weight: 500;
        color: rgba($black, 1);
      }
    }
  }

  &__item {
    padding-top: calculateRem(58px);
    border-top: 1px solid #D5D5D5;
    position: relative;
    margin-top: calculateRem(80px);
    padding-left: calculateRem(48px);

    &:before {
      content: '';
      @include size(calculateRem(20px));
      position: absolute;
      background-color: $yellow;
      top: calculateRem(-10px);
    }

    p {
      font-weight: 400;
      font-size: calculateRem(28px);
      line-height: calculateRem(36px);
      letter-spacing: -0.02em;
    }
  }

  &__count {
    font-weight: 400;
    font-size: calculateRem(96px);
    line-height: calculateRem(96px);
    letter-spacing: -0.04em;
    margin-bottom: calculateRem(24px);
  }

  @include media("<=wrapper") {
    &__text {
      p {
        font-size: 24px;
        line-height: 32px;
      }
    }

    &__item {
      p {
        font-size: 16px;
        line-height: 24px;
      }
    }

    &__count {
      font-size: 64px;
      line-height: 64px;
    }
  }

  @include media("<=tablet") {
    .section-title {
      padding-left: 20px;
      margin-bottom: 40px;
    }

    &__text {
      padding: 0 20px;
      margin-bottom: 20px;
    }

    &__item {
      padding: 20px;
      margin-top: 20px;

      &:before {
        @include size(12px);
        top: -6px;
      }

      p {
        font-size: 12px;
        line-height: 20px;
      }
    }

    &__count {
      font-size: 24px;
      line-height: 24px;
      font-weight: 500;
      margin-bottom: 4px;
    }
  }
}