.projects {
  padding-top: calculateRem(120px);
  $this: &;

  &__item {
    display: flex;
    margin-bottom: calculateRem(40px);

    &:hover {
      #{$this}__img {

        img {
          transform: scale(1.05);
        }
      }

      #{$this}__btn {
        &:before {
          left: 0;
        }
      }
    }
  }

  .section-title {
    margin-bottom: calculateRem(60px);
  }

  &__img {
    overflow: hidden;
    display: block;

    &:hover {
      img{
        transform: scale(1.05);
      }
    }

    img {
      @extend %transition;
      width: 100%;
    }
  }

  &__desc {
    max-width: calculateRem(455px);
    font-size: calculateRem(16px);
    line-height: calculateRem(24px);
    letter-spacing: -0.02em;
    margin: 0 auto calculateRem(40px);

    ul {
      display: flex;
      align-items: center;
      width: 100%;
      margin-top: calculateRem(40px);

      li {
        margin-right: calculateRem(12px);

        &:last-child {
          margin-right: 0;
        }

        a {
          font-weight: 500;
          padding: calculateRem(4px) calculateRem(16px);
          border: 1px solid $black;
          border-radius: calculateRem(100px);
          color: $black;
          display: flex;
          align-items: center;
          justify-content: center;

          &:hover {
            background-color: $black;
            color: $white;
          }
        }
      }
    }
  }

  &__btn {
    background-color: #F9F9F9;
    position: relative;
    overflow: hidden;

    &:hover {
      &:before {
        background-color: darken($yellow, 10%);
      }
    }

    svg {
      z-index: 1;
    }

    &:before {
      content: '';
      @include fullscreen();
      width: 100%;
      right: auto;
      left: -100%;
      background-color: $yellow;
      transition: inherit;
    }
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(72px);
    line-height: calculateRem(72px);
    letter-spacing: -0.02em;
    margin-bottom: calculateRem(32px);
    display: block;
    color: inherit;
  }

  @include media("<=wrapper") {
    &__img {
      width: 80%;
    }
    &__title {
      font-size: 40px;
      line-height: 48px;
    }

    &__desc {
      max-width: unset;
      margin-right: 50px;
    }
  }
  @include media("<=tablet") {
    padding-top: 40px;
    .section-title {
      margin-bottom: 32px;
    }

    &__btn {
      display: none;
    }

    &__img {
      width: 100%;
      margin-bottom: 20px;
    }

    &__desc {
      margin-right: 0;
      margin-left: 30px;

      p {
        display: none;
      }

      ul {
        margin-top: 0;

        li {
          &:nth-child(n+3) {
            display: none;
          }
        }
      }
    }

    &__title {
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.02em;
      margin-bottom: 16px;
    }
  }

  @include media("<=phone") {
    &__desc {
      margin-left: 0;
    }
  }
}