.contact-section {
  position: relative;
  margin-top: calculateRem(-120px);

  &:before {
    content: '';
    @include fullscreen();
    background: linear-gradient(180deg, rgba(27, 27, 30, 0) 0%, #1B1B1E 100%), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
  }

  &__text {
    text-align: center;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translateY(-60%) translateX(-50%);
    color: $white;
    padding-top: calculateRem(120px);


    h2 {
      color: $white;
      font-weight: 500;
      font-size: calculateRem(104px);
      line-height: calculateRem(104px);
      letter-spacing: -0.04em;
      margin-bottom: calculateRem(24px);
    }

    p {
      font-size: calculateRem(16px);
      line-height: calculateRem(24px);
      margin-bottom: calculateRem(48px);
    }
  }

  &__img {
    img {
      width: 100%;
    }
  }

  @include media("<=wrapper") {
    &__text {
      padding-top: 50px;

      h2 {
        font-size: 64px;
        line-height: 64px;
      }

      p {
        font-size: 14px;
        line-height: 24px;
      }
    }
  }

  @include media("<=tablet") {
    margin-top: 0;
    height: 500px;

    &__img {
      height: 100%;

      img {
        height: 100%;
        object-fit: cover;
      }
    }
    &__text {
      padding-top: 0;
      top: 50%;
      left: 20px;
      right: 20px;
      transform:translateY(-50%);

      h2 {
        font-size: 32px;
        line-height: 40px;
        letter-spacing: -0.04em;
      }

      p {
        font-weight: 400;
        font-size: 13px;
        line-height: 20px;
        letter-spacing: -0.01em;
        color: rgba($white, .8);
      }
    }
  }
}