.vacancies {
  &__body {
    margin-top: calculateRem(120px);
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(52px);
    line-height: calculateRem(60px);
    letter-spacing: -0.04em;
    margin-bottom: calculateRem(50px);
  }

  &__info {
    max-width: calculateRem(600px);

    li {
      font-weight: 500;
      font-size: calculateRem(18px);
      line-height: calculateRem(26px);
      margin-bottom: calculateRem(25px);
      display: flex;

      &:last-child {
        margin-bottom: 0;
      }

      &:before {
        content: '';
        @include size(calculateRem(20px));
        min-width: calculateRem(20px);
        background-color: $yellow;
        display: block;
        margin-right: calculateRem(25px);
      }
    }
  }

  @include media("<=wrapper") {
    &__info {
      padding-right: 50px;
    }
  }
  @include media("<=tablet") {
    &__body {
      margin-top: 40px;
    }

    &__title {
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.02em;
      margin-bottom: 24px;
    }

    &__info {
      padding-right: 0;
      margin-bottom: 40px;

      li {
        font-size: 16px;
        line-height: 24px;
        align-items: baseline;

        &:before {
          @include size(12px);
          min-width: 12px;
          margin-right: 17px;
        }
      }
    }
    .apply {
      margin-top: 0;
    }
  }
}