.burger-nav {
  @include fullscreen();
  position: fixed;
  @extend %transition;
  $this: &;
  z-index: 15;
  pointer-events: none;
  visibility: hidden;


  &.open {
    pointer-events: auto;
    visibility: visible;

    #{$this}__body {
      right: 0;
    }

    #{$this}__blur {
      opacity: 1;
      visibility: visible;
    }
  }

  * {
    position: relative;
    z-index: 5;
  }


  &__blur {
    background-color: rgba($black, .5);
    @include fullscreen();
    z-index: 1;
    @extend %transition;
    opacity: 0;
    visibility: hidden;
  }

  &__body {
    background-color: $yellow;
    padding: calculateRem(44px) calculateRem(48px);
    z-index: 2;
    width: 50%;
    height: 100%;
    position: absolute;
    @extend %transition;
    right: -50%;
    overflow-y: auto;
    @extend %scroll;
    display: flex;
    flex-direction: column;

    &:before {
      position: absolute;
      content: '';
      background-image: url("../images/pattern.svg");
      @include size(100%);
      background-repeat: no-repeat;
      background-position: center;
      top: 0;
      left: 0;
      background-size: cover;
    }
  }

  &__contact {
    margin-bottom: calculateRem(88px);

    a {
      font-weight: 500;
      font-size: calculateRem(18px);
      line-height: calculateRem(28px);
      border-bottom: 1px solid $black;
      margin-right: calculateRem(165px);
      color: $black;

      &:last-child {
        margin-right: 0;
      }

      &:hover {
        color: rgba($black, .5);
        border-bottom: 1px solid transparent;
      }
    }
  }

  &__close {
    padding: calculateRem(44px);
    background-color: $black;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 0;
    top: 0;

    svg {
      color: $white;
    }

    &:hover {
      background: lighten($black, 10%);
    }
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(18px);
    line-height: calculateRem(28px);
    letter-spacing: -0.04em;
    color: rgba($black, .5);
    margin-bottom: calculateRem(24px);
  }

  &__menu {
    margin-bottom: calculateRem(40px);

    &:last-child {
      margin-bottom: calculateRem(50px);
    }

    li {
      margin-bottom: calculateRem(8px);


      &:last-child {
        margin-bottom: 0;
      }

      a {
        color: $black;
        font-weight: 500;
        font-size: calculateRem(34px);
        line-height: calculateRem(44px);
        letter-spacing: -0.08em;
        border-bottom: 2px solid $black;

        &:hover {
          color: rgba($black, .5);
          border-bottom: 2px solid transparent;
        }
      }
    }
  }

  &__social {
    margin-top: auto;
    display: flex;
    align-items: center;
    background-color: $yellow;

    ul {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;


      li {
        width: calc(100% / 4);
        font-weight: 500;
        font-size: calculateRem(18px);
        line-height: calculateRem(28px);
        text-align: center;

        &:first-child {
          height: 100%;
          width: 100%;
          box-shadow: 1px 0 0 0 #E6C944,
          0 1px 0 0 #E6C944,
          1px 1px 0 0 #E6C944,
          1px 0 0 0 #E6C944 inset,
          0 1px 0 0 #E6C944 inset;
          display: flex;
          align-items: center;
          justify-content: center;
        }

        a {
          color: $black;
          display: flex;
          padding: calculateRem(33px);
          align-items: center;
          justify-content: center;
          box-shadow: 1px 0 0 0 #E6C944,
          0 1px 0 0 #E6C944,
          1px 1px 0 0 #E6C944,
          1px 0 0 0 #E6C944 inset,
          0 1px 0 0 #E6C944 inset;


          &:hover {
            background-color: $black;
            color: $white;
            box-shadow: 1px 0 0 0 $black,
            0 1px 0 0 $black,
            1px 1px 0 0 $black,
            1px 0 0 0 $black inset,
            0 1px 0 0 $black inset;
          }
        }
      }
    }
  }

  @include media("<=wrapper") {
    &__body {
      width: 75%;
      height: 750px;
      right: -75%;
    }
  }
  @include media("<=medium") {
    overflow-y: auto;
    background-color: $yellow;
    padding-bottom: 30px;
    left: auto;
    width: 100%;
    right: -100%;
    &.open {
      right: 0;
    }
    &__blur {
      display: none;
    }
    &__body {
      padding: 100px 20px 0;
      height: auto;
      width: auto;
      left: 0;
      top: 0;
      right: 0;
      bottom: 30px;
      overflow-y: unset;
    }

    &__close {
      display: none;
    }

    &__title {
      font-size: 14px;
      line-height: 24px;
      margin-bottom: 16px;
    }

    &__contact {
      order: 1;
      margin-bottom: 24px;
      display: flex;
      justify-content: space-between;

      a {
        margin-right: 0;
      }
    }

    &__menu {
      li {
        margin-bottom: 8px;

        a {
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.04em;
          border-bottom: 1px solid $black;
        }
      }
    }

    &__social {
      order: 2;

      ul {
        li {
          &:first-child {
            display: none;
          }

          a {
            padding: 30px;
          }
        }
      }
    }
  }
}
