.viewbox-container{
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(28, 26, 80, 0.75);
	background-blend-mode: multiply;
	z-index: 700;
}
.viewbox-body{
	position: absolute;
	top: 50%;
	left: 50%;
	//background: #fff;
	//-webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
	//-moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
	//box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
	//overflow: auto;
}
.viewbox-header{
	margin: 10px;
	display: none;
}
.viewbox-content{
	margin: 0px;
	width: 300px;
	height: 300px;
}
.viewbox-footer{
	margin: 10px;
	display: none;
}
.viewbox-content .viewbox-image{
	width: 100%;
	height: 100%;
}

/* buttons */
.viewbox-button-default{
	cursor: pointer;
	height: 40px;
	width: 40px;
}
.viewbox-button-default > svg{
	width: 100%;
	height: 100%;
	background: inherit;
	fill: inherit;
	pointer-events: none;
	transform: translateX(0px);
	color: white;
}
.viewbox-button-default{
	fill: white;
}
.viewbox-button-default:hover{
    fill: #fff;
}

.viewbox-button-close{
	position:absolute;
	top: -40px;
	right: -40px;
	z-index:9;
}
.viewbox-button-next,
.viewbox-button-prev{
	position:absolute;
	top: 50%;
	height: 62px;
    width: 62px;
	z-index:9;
	transform: translateY(-50%);
}
.viewbox-button-next{
	right: -60px;
}
.viewbox-button-prev{
	left: -60px;
}

/* loader */
.viewbox-container .loader{
	widows: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	margin:-25px 0 0 -25px;
}
.viewbox-container .loader *{
	margin: 0;
	padding: 0;
}
.viewbox-container .loader .spinner{
	width: 50px;
	height: 50px;
	position: relative;
	margin: 0 auto;
}
.viewbox-container .loader .double-bounce1,
.viewbox-container .loader .double-bounce2{
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-color: #999;
	opacity: 0.6;
	position: absolute;
	top: 0;
	left: 0;
	-webkit-animation: sk-bounce 2.0s infinite ease-in-out;
	animation: sk-bounce 2.0s infinite ease-in-out;
}
.viewbox-container .loader .double-bounce2 {
	-webkit-animation-delay: -1.0s;
	animation-delay: -1.0s;
}
@-webkit-keyframes sk-bounce{
	0%, 100% { -webkit-transform: scale(0.0) }
	50% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bounce{
	0%, 100% { 
		transform: scale(0.0);
		-webkit-transform: scale(0.0);
	} 50% { 
		transform: scale(1.0);
		-webkit-transform: scale(1.0);
	}
}
