.apply {
  margin-top: calculateRem(130px);
  padding: calculateRem(160px) 0 calculateRem(130px);
  background: linear-gradient(249.72deg, #F9F9F9 36.51%, rgba(249, 249, 249, 0) 84.72%);
  position: relative;

  &__pattern {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(54px);
    line-height: calculateRem(60px);
    letter-spacing: -0.04em;
    margin-bottom: calculateRem(40px);
    position: relative;

    &:before {
      content: '';
      @include size(calculateRem(20px));
      background-color: $yellow;
      position: absolute;
      top: calculateRem(-70px);
    }

    &:after {
      content: '';
      height: 1px;
      position: absolute;
      background-color: #D5D5D5;
      width: calculateRem(48px);
      left: calculateRem(-48px);
      top: calculateRem(-61px);
    }
  }

  &__head {
    margin-bottom: calculateRem(60px);
    max-width: calculateRem(600px);


    p {
      font-weight: 400;
      font-size: calculateRem(18px);
      line-height: calculateRem(26px);
      color: #7C7C7C;
    }
  }

  form {
    display: flex;
    max-width: calculateRem(1000px);

    .col-md-6 {
      &:nth-child(2) {
        margin-left: -1px;
      }

      &:nth-child(3) {
        margin-top: -1px;
      }

      &:nth-child(4) {
        margin-top: -1px;
        margin-left: -1px;
      }
    }

    .btn {
      margin-top: calculateRem(60px);
    }
  }

  @include media("<=wrapper") {
    &__pattern {
      width: 220px;
    }
  }

  @include media("<=tablet") {
    margin-top: 40px;
    padding-top: 60px;
    padding-bottom: 0;

    .col-md-9 {
      width: 100%;
    }


    .col-md-3 {
      display: none;
    }

    &__pattern {
      display: none;
    }

    &__head {
      margin-bottom: 32px;

      p {
        font-size: 13px;
        line-height: 20px;
      }
    }

    &__title {
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.02em;
      margin-bottom: 16px;

      &:before {
        top: -32px;
        @include size(12px);
      }

      &:after {
        top: -27px;
      }
    }

    form {
      display: block;

      .btn {
        width: auto;
        display: flex;
        margin: 24px -20px 0;
      }
    }
  }

  @include media("<=phone") {
    form {
      .col-md-6 {

        &:first-child {
          margin-bottom: -1px;
        }

        &:nth-child(2) {
          margin-left: 0;
          margin-bottom: -1px;
        }

        &:nth-child(3) {
          margin-top: 0;
        }

        &:nth-child(4) {
          margin-top: -1px;
          margin-left: 0;
        }
      }
    }
  }
}

.request-form {
  @include fullscreen();
  position: fixed;
  $this: &;
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  @extend %transition;
  z-index: 10;

  &.open {
    visibility: visible;
    pointer-events: auto;
    opacity: 1;

    #{$this}__body {
      right: 0;
    }
  }

  &__body {
    background-color: $white;
    padding: calculateRem(48px);
    z-index: 2;
    width: calculateRem(570px);
    position: absolute;
    @extend %transition;
    right: calculateRem(-570px);
    @extend %scroll;
    display: flex;
    flex-direction: column;
    bottom: 0;
  }

  &__blur {
    background-color: rgba($black, .5);
    @include fullscreen();
    z-index: 1;
    @extend %transition;
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(40px);
    line-height: calculateRem(48px);
    letter-spacing: -0.01em;
    margin-bottom: calculateRem(40px);
  }

  form {
    .form-group {
      margin-top: -1px;

      &:first-child {
        margin-top: 0;
      }
    }
  }

  @include media("<=medium") {
    &__body {
      width: 100%;
      padding: 20px;
      right: -100%;
    }

    .burger-nav__close {
      padding: 20px;
    }

    &__title {
      margin-bottom: 20px;
    }
  }
}