.faq {
  margin: calculateRem(100px) 0;


  &__item {
    margin-bottom: calculateRem(60px);
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(26px);
    line-height: calculateRem(34px);
    letter-spacing: -0.04em;
    position: relative;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #DADADA;
    color: $black;


    &:after {
      content: '';
      padding: calculateRem(60px);
      background-color: #F9F9F9;
      margin-left: calculateRem(120px);
      background-image: url("../images/svg-icons/plus.svg");
      background-repeat: no-repeat;
      background-position: center;
      @extend %transition;
    }

    &:hover {
      border-color: $yellow;

      &:after {
        background-color: $yellow;
      }
    }

    &--active {
      border-color: $yellow;

      &:after {
        background-color: $yellow;
        background-image: url("../images/svg-icons/close-black.svg");
      }
    }
  }

  &__desc {
    padding: calculateRem(40px);
    border: 2px solid $yellow;
    display: none;
    border-top: 0;

    p {
      margin-bottom: 0 !important;
    }
  }

  @include media("<=wrapper") {
    &__title {
      font-size: 20px;
      line-height: 26px;
    }
  }
  @include media("<=tablet") {
    margin-top: 20px;

    &__title {
      font-weight: 600;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: -0.02em;
      align-items: center;

      &:after {
        margin-left: 20px;
        padding: 44px 24px;
        background-size: 18px;
      }
    }

    &__desc {
      padding: 20px;

      p {
        font-size: 13px !important;
        line-height: 20px !important;
      }
    }
  }
}