.footer {
  background-color: $black;
  padding: calculateRem(80px) 0;

  &__head {
    display: flex;
  }

  &__logo {
    min-width: calculateRem(200px);
    display: block;
    width: calculateRem(200px);
    filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(2%) hue-rotate(142deg) brightness(108%) contrast(101%);

    img {
      @include size(100%);
    }
  }

  &__nav {
    display: flex;
  }

  &__menu {
    flex: 1;
    //@include column(3, calculateRem(100px));
    //margin-left: calculateRem(100px);

    li {
      margin-bottom: calculateRem(12px);

      &:last-child {
        margin-bottom: 0;
      }

      a {
        font-weight: 400;
        font-size: calculateRem(24px);
        line-height: calculateRem(32px);
        letter-spacing: -0.02em;
        color: $white;
        white-space: nowrap;

        &:hover {
          color: $yellow;
        }
      }
    }
  }

  &__bottom {
    margin-top: calculateRem(170px);
    color: rgba($white, .5);
    font-weight: 400;
    font-size: calculateRem(16px);
    line-height: calculateRem(24px);
    display: flex;
    justify-content: space-between;
    border: 1px solid rgba($white, .2);

    > li {
      display: flex;

      > a:not(.jis) {

        color: $white;
        border-left: 1px solid rgba($white, .2);
        @extend %displayFlex;
        justify-content: center;
        @include size(calculateRem(152px));

        &:hover {
          background-color: $yellow;
          color: $black;
        }
      }


      .jis {
        padding: 0;
        border-right: 0;
        font-weight: bold;
        margin-right: 5px;
        display: flex;
        align-items: center;
        color: inherit;

        img {
          margin: 0 calculateRem(6px);
        }
      }
    }
  }

  &__copy, &__site-by {
    display: flex;
    align-items: center;
    height: 100%;
  }

  &__social {
    display: flex;
    height: 100%;

    li {
      width: calc(100% / 4);
      height: 100%;

      a {
        color: $white;
        border: 1px solid transparent;
        border-left-color: rgba($white, .2);
        @extend %displayFlex;
        justify-content: center;
        @include size(100%);

        &:hover {
          border: 1px solid $white;
        }
      }
    }
  }

  &__lang {
    border: 1px solid transparent;
    border-right-color: rgba($white, .2);
    color: rgba($white, .5);
    @extend %displayFlex;
    justify-content: center;
    @include size(calculateRem(152px));

    &:hover {
      border: 1px solid $white;
    }
  }

  &__site-by {
    .jis {
      padding: 0;
      border-right: 0;
      font-weight: bold;
      margin-right: 5px;
      display: flex;
      align-items: center;
      color: inherit;

      img {
        margin: 0 calculateRem(6px);
      }
    }

    span {
      margin-right: calculateRem(140px);
      display: block;
    }
  }

  @include media("<=widescreen") {
    &__lang {
      @include size(calculateRem(100px))
    }
    &__site-by {
      span {
        margin-right: 50px;
      }
    }
  }

  @include media("<=wrapper") {
    &__logo {
      min-width: 100px;
      width: 100px;
    }
    &__menu {
      li {
        a {
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
    &__bottom {
      margin-top: 100px;
    }
  }

  @include media("<=tablet") {
    padding: 32px 0 55px;
    &__logo {
      width: 120px;
      margin-bottom: 40px;
    }

    &__nav {
      flex-wrap: wrap;
    }

    &__head {
      .col-lg-3 {
        &:nth-child(2) {
          order: 1;
          margin-top: 16px;
        }
      }
    }

    .jis {
      img {
        margin: 0 8px !important;
      }
    }

    &__lang {
      border-left: 1px solid rgba($white, .2);
    }

    &__menu {
      margin-left: 0;
      margin-right: 20px;

      li {
        margin-bottom: 16px;
      }
    }

    &__bottom {
      flex-wrap: wrap;
      border-left: 0;
      margin-top: 40px;

      li {
        width: 25%;
        height: 80px;
        justify-content: center;
        align-items: center;

        a:not(.jis) {
          width: 100%;
          height: 100%;
        }
      }
    }

    &__site-by {
      border: 1px solid rgba($white, .2);
      justify-content: center;
      height: 80px;
    }

    &__copy {
      flex: 1;
      width: auto !important;
      justify-content: center;
    }
  }
}