.partners {
  margin-top: calculateRem(90px);
  padding: calculateRem(80px) 0 calculateRem(120px);
  position: relative;
  z-index: 1;
  overflow: hidden;

  &:before {
    content: '';
    @include fullscreen();
    width: 90%;
    background-color: $gray;
    z-index: -1;
    pointer-events: none;
  }

  .section-menu {
    display: flex;
    margin-bottom: calculateRem(54px);

    li {
      margin-right: calculateRem(40px);

      &:last-child {
        margin-right: 0;
      }

      a {
        color: rgba($white, .3);

        &.active, &:hover {
          color: rgba($white, 1);
        }
      }
    }
  }

  &__pattern {
    position: absolute;
    width: calculateRem(440px);
    right: 0;
    pointer-events: none;
  }

  &__btn {
    font-weight: 500;
    font-size: calculateRem(24px);
    line-height: calculateRem(32px);
    letter-spacing: -0.04em;
    z-index: 2;
    border: 0;

    span {
      transform: rotate(270deg);
    }
  }

  .section-title {
    color: $white;
    margin-bottom: calculateRem(80px);
  }

  &__row {
    display: flex;
    flex-wrap: wrap;

    li {
      width: 33.3%;
      padding-bottom: 33%;
      position: relative;
      display: block;
      box-shadow: 1px 0 0 0 #BEBEBE,
      0 1px 0 0 #BEBEBE,
      1px 1px 0 0 #BEBEBE,
      1px 0 0 0 #BEBEBE inset,
      0 1px 0 0 #BEBEBE inset;
      @extend %transition;

      &:hover {
        box-shadow: 1px 0 0 0 $white,
        0 1px 0 0 $white,
        1px 1px 0 0 $white,
        1px 0 0 0 $white inset,
        0 1px 0 0 $white inset;
        z-index: 1;
      }

      &:after, &:before {
        content: '';
        position: absolute;
        @include size(calculateRem(20px));
        background-color: #BEBEBE;
        top: calculateRem(-10px);
        left: calculateRem(-10px);
      }

      &:after {
        bottom: calculateRem(-10px);
        top: unset;
      }

      a {
        @include fullscreen();


        &:after, &:before {
          content: '';
          position: absolute;
          @include size(calculateRem(20px));
          background-color: #BEBEBE;
          top: calculateRem(-10px);
          right: calculateRem(-10px);
        }

        &:after {
          bottom: calculateRem(-10px);
          top: unset;
        }

        img {
          @include center();
        }
      }
    }
  }

  &__item {
    opacity: 0;
    @extend %transition;
    visibility: hidden;
    position: relative;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }


  .tab-item {
    display: flex;
    justify-content: space-between;
  }

  &__block {
    flex: 1;
    margin-right: calculateRem(213px);
  }

