  @include media("<=wrapper") {
    &:before {
      width: 88%;
    }
    &__pattern {
      width: 230px;
      right: -20px;
    }

    &__block {
      margin-right: calculateRem(80px);
    }
  }
  @include media("<=tablet") {
    padding: 32px 0 0;

    &:before {
      width: 100%;
    }

    .section-title {
      margin-bottom: 24px;
    }

    .section-menu {
      margin-bottom: 32px;
    }

    &__pattern {
      display: none;
    }

    .tab-item {
      flex-direction: column;
    }

    &__row {
      li {
        width: 50%;

        &:before, &:after {
          @include size(12px);
          top: -6px;
          left: -6px;
        }

        &:after {
          top: auto;
          bottom: -6px;
        }

        a {
          &:before, &:after {
            @include size(12px);
            top: -6px;
            right: -6px;
          }

          &:after {
            top: auto;
            bottom: -6px;
          }
        }
      }
    }

    &__item {
      img {
        max-width: 50%;
        max-height: 50%;
      }
    }

    &__block {
      margin-right: 0;
    }

    &__btn {
      width: auto;
      justify-content: center;
      padding: 68px;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.04em;
      margin: 40px -20px 0;

      span {
        transform: unset;
      }
    }
  }
}