body {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  overflow-x: hidden;
  position: relative;
}

#all_parts {
  flex: 1;
}

html {
  font-size: $font-size-base;
  @include media("<=laptop") {
    font-size: 53%;
  }
  @include media("<=wrapper") {
    font-size: 50%;
  }

  @include media("<=tablet") {
    font-size: 50%;
  }
}

p {
  margin-bottom: 0;
}

a {
  text-decoration: none !important;
}

ul {
  @extend %listreset
}

hr {
  margin: 0;
  border-color: #ECEDF0;
}

.container-fluid {
  max-width: 1920px;
  padding: 0 calculateRem(48px);
  @include media("<=tablet") {
    padding: 0 20px;
  }
}

.center {
  width: max-content;
  margin: 0 auto;
}

.pages-head {
  display: flex;
  color: $black;

  p {
    font-weight: 500;
    font-size: calculateRem(38px);
    line-height: calculateRem(56px);
    letter-spacing: -0.04em;
    max-width: calculateRem(560px);
  }

  span {
    font-weight: 400;
    font-size: calculateRem(20px);
    line-height: calculateRem(26px);
    color: inherit;
    margin-bottom: calculateRem(16px);
    display: block;
  }

  ul {
    display: flex;
    align-items: center;
    margin-top: calculateRem(40px);
    color: inherit;

    li {
      margin-right: calculateRem(10px);

      &:last-child {
        margin-right: 0;
      }

      a {
        display: block;
        font-weight: 500;
        font-size: calculateRem(16px);
        line-height: calculateRem(26px);
        letter-spacing: -0.04em;
        padding: calculateRem(6px) calculateRem(18px);
        border: 1px solid $black;
        border-radius: calculateRem(100px);
        color: $black;

        &:hover {
          background-color: $black;
          color: $white;
        }
      }
    }
  }

  .pages-pattern {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    pointer-events: none;

    img {
      height: 100%;
    }
  }

  @include media("<=wrapper") {
    p {
      font-size: 26px;
      line-height: 40px;
    }
    ul {
      li {
        font-size: 14px;
        padding: 4px 16px;
      }
    }

    .pages-pattern {
      height: 270px !important;
    }
  }
  @include media("<=tablet") {
    .pages-title {
      margin-bottom: 40px;
    }

    ul {
      li {
        &:last-child {
          display: none;
        }
      }
    }
    p {
      font-size: 24px;
      line-height: 32px;
      letter-spacing: -0.04em;
    }

    .pages-pattern {
      height: 300px !important;
      left: -130px;
    }
  }
}

.pages-title {
  font-weight: 500;
  font-size: calculateRem(96px);
  line-height: calculateRem(96px);
  letter-spacing: -0.04em;
  @include media("<=wrapper") {
    font-size: 64px;
    line-height: 64px;
  }
  @include media("<=tablet") {
    font-size: 40px;
    line-height: 40px;
    letter-spacing: -0.04em;
  }
}

.section-title {
  font-weight: 500;
  font-size: calculateRem(24px);
  line-height: calculateRem(32px);
  letter-spacing: -0.02em;
  @include media("<=tablet") {
    font-size: 14px;
    line-height: 20px;
  }
}

.section-menu {
  li {
    margin-bottom: calculateRem(16px);

    &:last-child {
      margin-bottom: 0;
    }

    a {
      font-weight: 500;
      font-size: calculateRem(104px);
      line-height: calculateRem(104px);
      letter-spacing: -0.04em;
      color: rgba($black, .3);

      &.active, &:hover {
        color: rgba($black, 1);
      }
    }
  }

  @include media("<=wrapper") {
    li {
      margin-bottom: 8px;

      a {
        font-size: 72px;
        line-height: 72px;
      }
    }
  }
  @include media("<=tablet") {
    display: flex;
    align-items: center;
    li {
      margin-bottom: 0;
      margin-right: 24px;

      &:last-child {
        margin-right: 0;
      }

      a {
        font-size: 24px;
        line-height: 24px;
      }
    }
  }
}

.bg-lightgray {
  background-color: #EBEBEB !important;
}


.small-title {
  font-size: calculateRem(40px);
  line-height: calculateRem(48px);
  max-width: calculateRem(990px);
}


.news-head {
  background-image: url("../images/news-inner-pattern.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  height: calculateRem(600px);


  .header__logo {
    img {
      filter: brightness(0) saturate(100%) invert(5%) sepia(1%) saturate(6001%) hue-rotate(203deg) brightness(102%) contrast(87%);
    }
  }
}


.other-block {
  margin-top: calculateRem(140px);
  margin-bottom: calculateRem(130px);

  &__title {
    font-weight: 500;
    font-size: calculateRem(22px);
    line-height: calculateRem(30px);
    letter-spacing: -0.03em;
    margin-bottom: calculateRem(48px);
  }

  @include media("<=tablet") {
    margin-top: 50px;
    &__title {
      margin-bottom: 24px;
    }
  }
}

.about-head {
  height: 100vh;
  background-color: $yellow;

  .pages-pattern2 {
    position: absolute;
    bottom: calculateRem(70px);
    left: 50%;
    transform: translateX(-10%);
  }

  .pages-title {
    max-width: calculateRem(1300px);
  }

  .pages-pattern {
    height: calculateRem(500px);
  }

  .header__logo {
    img {
      filter: brightness(0) saturate(100%) invert(5%) sepia(1%) saturate(6001%) hue-rotate(203deg) brightness(102%) contrast(87%);
    }
  }

  .header__main-menu {
    li {
      a {
        &:hover {
          color: $white !important;
        }
      }
    }
  }

  @include media("<=wrapper") {
    height: 650px;
    .pages-pattern2 {
      left: auto;
      right: 32px;
      transform: unset;
      bottom: 20px;
    }
  }

  @include media("<=tablet") {
    height: 600px;
    .pages-pattern2 {
      width: 300px;
      left: 40px;
    }
  }

  @include media("<=phone") {
    .pages-pattern {
      display: none;
    }

    .pages-pattern2 {
      width: 280px;
      left: 50%;
      transform: translateX(-50%);
      bottom: -82px;
    }

    .pages-title {
      font-size: 32px;
      line-height: 40px;
      letter-spacing: -0.04em;
      margin-bottom: 24px;
    }

    .header__content {
      margin-top: 0;
      display: flex;
      flex-direction: column-reverse;
    }
  }
}

.about-text {
  font-weight: 400;
  max-width: calculateRem(400px);
  font-size: calculateRem(16px);
  line-height: calculateRem(26px);
  position: absolute;
  right: calculateRem(60px);
  bottom: calculateRem(100px);
  @include media("<=wrapper") {
    bottom: 100px;
    left: 120px;
  }

  @include media("<=tablet") {
    right: 20px;
    left: auto;
  }

  @include media("<=phone") {
    position: unset;
  }
}

@include media("<=tablet") {
  .projects-inner__head {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;

    .header__logo {
      filter: brightness(0) saturate(100%) invert(100%) sepia(92%) saturate(71%) hue-rotate(264deg) brightness(110%) contrast(101%);
    }

    .header__lang {
      a {
        color: $white;
      }
    }

    .header__burger {
      i {
        background-color: $white;

        &:after {
          background-color: $white;
        }
      }
    }

    .pages-head {
      .pages-title {
        color: $white;
      }

      .pages-pattern {
        display: none;
      }

      ul {
        li {
          color: $white;
          border-color: $white;
        }
      }
    }
  }
}

.tab-content {
  position: relative;

  &__item {
    position: absolute;
    opacity: 0;
    visibility: hidden;
    @extend %transition;

    &.active {
      opacity: 1;
      visibility: visible;
    }
  }
}
