.h-r {
  &__banner {
    position: relative;
  }

  &__img {
    height: 100%;

    img {
      @include size(100%);
      object-fit: cover;
      object-position: right;
    }
  }

  &__title {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: calculateRem(48px);
    color: $white;
    font-weight: 500;
    font-size: calculateRem(104px);
    line-height: calculateRem(104px);
    letter-spacing: -0.04em;
    margin: auto 0;

    span {
      display: block;
      padding-left: calculateRem(120px);
    }
  }

  &__desc {
    background-color: $black;
    height: 100%;
    color: $white;
    padding: calculateRem(150px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url("../images/human-resurces-pattern.svg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  &__text {
    max-width: calculateRem(500px);

    p {
      font-weight: 400;
      font-size: calculateRem(18px);
      line-height: calculateRem(26px);
      margin-bottom: calculateRem(26px);

      a {
        color: inherit;

        &:hover {
          color: $yellow;
        }
      }
    }

    .btn {
      width: 100%;
      margin-top: calculateRem(40px);
    }
  }

  &__vacancies {
    margin-top: calculateRem(120px);

    .section-title {
      margin-bottom: calculateRem(80px);
    }

    &__item {
      display: block;
      padding: calculateRem(50px);
      box-shadow: 1px 0 0 0 #E7E7E7,
      0 1px 0 0 #E7E7E7,
      1px 1px 0 0 #E7E7E7,
      1px 0 0 0 #E7E7E7 inset,
      0 1px 0 0 #E7E7E7 inset;
      height: 100%;
      color: $black;

      &:hover {
        background-color: $yellow;

        .h-r__vacancies__date {
          color: rgba($black, .8);
        }
      }
    }

    &__date {
      font-weight: 400;
      font-size: calculateRem(24px);
      line-height: calculateRem(32px);
      color: #7C7C7C;
      margin-bottom: calculateRem(20px);
      display: block;
    }

    &__title {
      font-weight: 500;
      font-size: calculateRem(40px);
      line-height: calculateRem(48px);
      letter-spacing: -0.04em;
      margin-bottom: calculateRem(80px);
    }

    &__info {
      display: flex;
      align-items: center;

      li {
        font-weight: 500;
        font-size: 16px;
        line-height: 26px;
        letter-spacing: -0.04em;
        padding: calculateRem(6px) calculateRem(18px);
        border: 1px solid $black;
        border-radius: calculateRem(100px);
        margin-right: calculateRem(10px);

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  @include media("<=wrapper") {
    &__title {
      font-size: 64px;
      line-height: 64px;

      span {
        padding-left: 80px;
      }
    }

    &__desc {
      padding: 150px 100px;
    }

    &__vacancies {
      margin-top: 80px;
    }
  }

  @include media("<=tablet") {
    &__desc {
      padding: 160px 20px 0;
    }

    &__title {
      position: unset;
      font-size: 40px;
      line-height: 40px;
      letter-spacing: -0.04em;

      span {
        display: inline-block;
        padding: 0;
      }
    }

    &__text {
      p {
        font-size: 13px;
        line-height: 20px;
        margin-bottom: 12px;
      }

      .btn {
        margin: 90px -20px 0;
        width: auto;
        display: flex;
      }
    }

    &__vacancies {
      margin-top: 32px;

      .section-title {
        margin-bottom: 32px;
      }

      &__item {
        padding: 20px;
      }

      &__date {
        font-size: 13px;
        line-height: 20px;
      }

      &__title {
        font-size: 20px;
        line-height: 28px;
        letter-spacing: -0.02em;
        margin-bottom: 40px;
      }

      &__info {
        li {
          font-size: 13px;
          line-height: 24px;
          letter-spacing: -0.02em;
          padding: 4px 16px;
        }
      }
    }
  }

  @include media("<=phone") {
    &__img {
      display: none;
    }
  }
}


