.about {
  margin-top: calculateRem(130px);

  h3 {
    font-weight: 500;
    font-size: calculateRem(48px);
    line-height: calculateRem(54px);
    letter-spacing: -0.02em;
  }

  p {
    font-weight: 400;
    font-size: calculateRem(16px);
    line-height: calculateRem(26px);
    letter-spacing: -0.01em;
    color: #7C7C7C;
    max-width: calculateRem(400px);
  }

  .section-title {
    margin-bottom: calculateRem(65px);
  }

  .projects {
    padding: 0;
    counter-reset: my-awesome-counter;


    &__img {
      height: calculateRem(400px);
      width: calculateRem(550px);
    }

    &__desc {
      max-width: max-content;
      margin: 0;
      counter-increment: my-awesome-counter;
      position: relative;
      font-weight: 400;
      font-size: calculateRem(22px);
      line-height: calculateRem(34px);

      &:before {
        content: counter(my-awesome-counter, decimal-leading-zero);
        position: absolute;
        left: calculateRem(-120px);
        top: 10px;
        color: $black;
      }

      p {
        font-weight: 400;
        font-size: calculateRem(26px);
        line-height: calculateRem(34px);
        color: #7C7C7C;

      }
    }

    &__title {
      font-size: calculateRem(48px);
      line-height: calculateRem(54px);
      letter-spacing: -0.02em;
      margin-bottom: calculateRem(20px);
      color: $black;
    }
  }

  .vision {
    padding: calculateRem(125px) 0;
    background-color: #F9F9F9;
    margin-top: calculateRem(60px);

    &__left {
      p {
        font-weight: 500;
        font-size: calculateRem(26px);
        line-height: calculateRem(34px);
        letter-spacing: -0.01em;
        color: $black;
        max-width: calculateRem(500px);
        margin-bottom: calculateRem(50px);
      }
    }

    &__right {
      &__item {
        display: flex;
        justify-content: space-between;
        margin-bottom: calculateRem(100px);
        padding-top: calculateRem(50px);
        border-top: 1px solid #D5D5D5;
        position: relative;

        &:before {
          content: '';
          position: absolute;
          top: -10px;
          @include size(calculateRem(20px));
          background-color: $yellow;
        }

        &:last-child {
          margin-bottom: 0;
        }

        p {
          max-width: calculateRem(250px);
          margin-right: calculateRem(200px);
        }
      }
    }
  }

  .perspectives {
    margin-top: calculateRem(-65px);

    &__img {
      img {
        @include size(100%);
      }
    }

    &__text {
      margin-left: calculateRem(125px);
      flex: 1;
      max-width: calculateRem(500px);
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      h3 {
        margin-bottom: calculateRem(70px);
      }
    }
  }

  .values {
    margin-top: calculateRem(210px);

    .section-title {
      margin-bottom: calculateRem(150px);
    }

    &__item {
      h3 {
        margin-bottom: calculateRem(32px);
      }

      &:hover {
        .values__icon{
          background-color: $yellow;
        }
      }
    }

    &__icon {
      @include size(calculateRem(120px));
      background-color: #F1F1F1;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: calculateRem(50px);
      @extend %transition;
    }
  }

  .contact-section {
    margin-top: calculateRem(130px);

    &__text {
      p {
        font-weight: 400;
        font-size: calculateRem(18px);
        line-height: calculateRem(26px);
        max-width: unset;
        color: $white;
      }
    }
  }

  @include media("<=wrapper") {
    .projects {
      &__desc {
        max-width: 300px;
        margin-left: 120px;

        &:before {
          left: -70px;
        }
      }
    }

    .vision {
      &__left {
        &__pattern {
          width: 300px;
        }
      }

      &__right {
        &__item {
          p {
            margin-right: 50px;
          }
        }
      }
    }

    .perspectives {
      &__text {
        h3 {
          font-size: 32px;
          line-height: 36px;
          margin-bottom: 30px;
        }
      }
    }

    .values {
      margin-top: 100px;

      .section-title {
        margin-bottom: 80px;
      }

      &__item {
        margin-right: 30px;
      }
    }
  }

  @include media("<=tablet") {
    margin-top: 32px;

    .section-title {
      margin-bottom: 32px;
    }

    .projects {
      &__img {
        width: 100%;
        height: 280px;
        margin-bottom: 16px;

        img {
          height: 100%;
          object-fit: cover;
        }
      }

      &__desc {
        margin-bottom: 24px;

        p {
          display: block;
          font-size: 16px;
          line-height: 24px;
        }

        &:before {
        }
      }

      &__title {
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.02em;
        margin-bottom: 8px;
      }
    }

    .vision {
      padding-top: 32px;
      overflow: hidden;

      &__left {
        margin-right: 40px;

        p {
          font-weight: 600;
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.01em;
          margin-bottom: 15px;
        }

        &__pattern {
          width: 250px;
        }
      }

      &__right {
        &__item {
          padding-top: 22px;
          flex-direction: column;
          margin-bottom: 24px;
          margin-right: -20px;

          &:before {
            @include size(12px);
            top: -7px;
          }

          h3 {
            font-size: 32px;
            line-height: 40px;
            letter-spacing: -0.04em;
            margin-bottom: 16px;
            padding-right: 20px;
          }

          p {
            margin-right: 0;
            max-width: 100%;
            padding-right: 20px;
          }
        }
      }
    }

    .perspectives {
      &__img {
        margin-bottom: 24px;
      }

      &__text {
        margin-left: 40px;
        flex: 1;

        h3 {
          font-size: 24px;
          line-height: 32px;
          letter-spacing: -0.02em;
          margin-bottom: 16px;
        }

        p {
          font-size: 13px;
          line-height: 20px;
          letter-spacing: -0.02em;
        }
      }
    }

    .values {
      margin-top: 40px;

      .section-title {
        margin-bottom: 40px;
      }

      &__item {
        margin-right: 0;
        margin-bottom: 40px;

        h3 {
          font-size: 24px;
          line-height: 32px;
          letter-spacing: -0.04em;
        }

        p {
          font-weight: 500;
          font-size: 13px;
          line-height: 20px;
          letter-spacing: -0.02em;
          max-width: 100%;
        }
      }

      &__icon {
        margin-bottom: 24px;
      }
    }

    .contact-section {
      margin-top: 0;
    }
  }

  @include media("<=phone") {
    .projects {
      &__desc {
        margin-left: 0;

        &:before {
          display: none;
        }
      }
    }

    .vision {
      &__left {
        &__pattern {
          display: none;
        }
      }
    }

    .perspectives {
      &__text {
        margin-left: 0;
      }
    }
  }
}