.banner {
  height: 100vh;
  position: relative;
  overflow: hidden;

  &:before {
    content: '';
    position: absolute;
    @include size(100%);
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.8) 100%), rgba(0, 0, 0, 0.3);
    z-index: 2;
  }

  &__video {
    position: relative;

    video {
      position: absolute;
      left: 0;
      top: 0;
      object-fit: cover;
      width: 100vw;
      height: 100vh;
    }
  }

  &__img {
    @include size(100%);
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    @extend %transition;
    z-index: 1;

    &.show {
      opacity: 1;
    }

    img {
      transition: inherit;
      @include size(100%);
      object-fit: cover;
    }
  }

  &__text {
    @include fullscreen();
    color: $white;
    z-index: 3;
  }

  &__head {
    display: flex;
  }

  .container-fluid {
    height: 100%;
  }

  .row {
    height: 100%;

    .col-md-6 {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;

    }
  }

  &__bottom-text {
    position: absolute;
    bottom: calculateRem(80px);
    font-weight: 400;
    font-size: calculateRem(16px);
    line-height: calculateRem(24px);
    letter-spacing: -0.01em;
    color: rgba($white, .8);
    max-width: calculateRem(455px);
    margin-left: calculateRem(155px);
  }

  &__title {
    color: $white;
    font-weight: 500;
    font-size: calculateRem(104px);
    line-height: calculateRem(104px);
    letter-spacing: -0.04em;
    margin: auto 0;

    span {
      display: block;
      padding-left: calculateRem(120px);
    }
  }

  &__menu {
    list-style: none;
    font-weight: 400;
    font-size: calculateRem(16px);
    line-height: calculateRem(24px);
    counter-reset: my-awesome-counter;
    padding: 0;
    @extend %transition;

    &:hover {
      li {
        color: rgba($white, .5);

        &:hover {
          color: rgba($white, 1);
        }
      }
    }

    li {
      margin-bottom: calculateRem(24px);
      counter-increment: my-awesome-counter;
      display: flex;
      align-items: flex-start;
      color: $white;
      @extend %transition;

      &:before {
        content: counter(my-awesome-counter, decimal-leading-zero);
        margin-right: calculateRem(135px);
        @extend %transition;
      }

      &:last-child {
        margin-bottom: 0;
      }

      a {
        font-weight: 400;
        font-size: calculateRem(56px);
        line-height: calculateRem(56px);
        letter-spacing: -0.04em;
        color: inherit;
      }
    }
  }

  &__scroll {
    display: flex;
    align-items: center;
    color: rgba($white, .8);
    margin-bottom: calculateRem(85px);
    font-weight: 400;
    font-size: calculateRem(16px);
    line-height: calculateRem(24px);
    letter-spacing: -0.01em;

    &:hover {
      color: rgba($white, 1);

    }

    span {
      @include size(calculateRem(40px));
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      border: 1px solid $white;
      margin-right: calculateRem(16px);
    }
  }

  @include media("<=laptop") {

    &__scroll {
      margin-bottom: calculateRem(110px);
    }

    .banner__bottom-text {
      bottom: calculateRem(110px);
    }
    .row {
      .col-md-6 {
        &:last-child {
          padding-left: 100px;
        }
      }
    }
  }

  @include media("<=wrapper") {
    height: 685px;
    &__title {
      font-size: 64px;
      line-height: 64px;

      span {
        padding-left: 80px;
      }
    }

    &__menu {
      li {
        &:before {
          margin-right: 60px;
        }

        a {
          font-size: 32px;
          line-height: 40px;
        }
      }
    }

    &__bottom-text {
      margin-left: 80px;
    }
  }

  @include media("<=tablet") {
    height: 550px;

    .row {
      flex-direction: column;
    }

    &__title {
      font-size: 40px;
      line-height: 40px;
      letter-spacing: -0.04em;
      margin-bottom: 44px;

      span {
        padding: 0;
      }
    }

    &__scroll {
      display: none;
    }

    .row {
      height: auto;
    }

    .col-md-6 {
      p {
        display: none;
      }

      &:last-child {
        padding-left: 0 !important;
      }
    }

    &__text {
      top: auto;
      bottom: 60px;
    }

    &__menu {
      border: 1px solid rgba($white, .2);
      display: flex;
      flex-wrap: wrap;

      li {
        flex-direction: column;
        padding: 16px;
        margin-bottom: 0;
        width: calc(100% / 2);
        border-right: 1px solid rgba($white, .2);
        border-bottom: 1px solid rgba($white, .2);

        &:before {
          margin-right: 0;
          margin-bottom: 44px;
        }

        a {
          font-size: 16px;
          line-height: 24px;
          letter-spacing: -0.04em;
        }
      }
    }
  }

}