.news {
  margin: calculateRem(100px) 0;

  $this: &;

  &__item {
    box-shadow: 1px 0 0 0 #E7E7E7,
    0 1px 0 0 #E7E7E7,
    1px 1px 0 0 #E7E7E7,
    1px 0 0 0 #E7E7E7 inset,
    0 1px 0 0 #E7E7E7 inset;
    height: 100%;
    position: relative;
    display: block;
    overflow: hidden;

    @include media(">tablet") {

      &:hover {
        #{$this}__img {
          visibility: visible;
          opacity: 1;
        }

        #{$this}__text {
          color: $white;
        }

        #{$this}__date {
          color: rgba($white, .8);
        }
      }
    }
  }

  &__text {
    @include fullscreen();
    padding: calculateRem(40px);
    display: flex;
    flex-direction: column;
    color: $black;
    @extend %transition;
  }

  &__img {
    padding-bottom: 62%;
    min-height: calculateRem(400px);
    background-size: cover;
    position: relative;
    visibility: hidden;
    opacity: 0;
    @extend %transition;

    &:before {
      position: absolute;
      content: '';
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.35), rgba(0, 0, 0, 0.35));
      @include size(100%);
    }

    img {
      width: 100%;
    }
  }

  &__date {
    display: block;
    font-weight: 400;
    font-size: calculateRem(20px);
    line-height: calculateRem(26px);
    color: #7C7C7C;
    margin-bottom: calculateRem(32px);
  }

  &__title {
    font-weight: 500;
    font-size: calculateRem(40px);
    line-height: calculateRem(48px);
    letter-spacing: -0.04em;
    margin-bottom: calculateRem(32px);
    color: inherit;
  }

  &__category {
    display: flex;
    align-items: center;
    margin-top: auto;

    li {
      color: inherit;
      font-weight: 500;
      font-size: calculateRem(16px);
      line-height: calculateRem(26px);
      letter-spacing: -0.04em;
      padding: calculateRem(6px) calculateRem(18px);
      border: 1px solid;
      border-color: inherit;
      border-radius: calculateRem(100px);
      margin-right: calculateRem(10px);

      &:last-child {
        margin-right: 0;
      }
    }
  }

  @include media("<=wrapper") {
    &__title {
      font-size: 24px;
      line-height: 32px;
    }
  }
  @include media("<=tablet") {
    margin-top: 20px;

    &__text {
      padding: 20px;
      position: unset;
    }

    &__img {
      display: none;
    }

    &__date {
      font-size: 13px;
      line-height: 20px;
      margin-bottom: 8px;
    }

    &__title {
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.02em;
      margin-bottom: 32px;
    }

    .btn-full {
      padding: 28px;
    }

    &__category {
      li {
        &:last-child {
          display: none;
        }
      }
    }
  }
  @include media("<=tablet") {

  }
}