.location {
  padding-top: calculateRem(80px);

  &__pattern {
    position: absolute;
    left: 0;
    bottom: 0;
    pointer-events: none;
  }

  .section-title {
    padding-left: calculateRem(48px);
    margin-bottom: calculateRem(100px);
  }

  .section-menu {
    padding-left: calculateRem(48px);
  }

  &__info {
    display: flex;
    margin-top: calculateRem(135px);

    &__item {
      padding-top: calculateRem(50px);
      position: relative;
      border-top: 1px solid transparent;


      &:first-child {
        border-top: 1px solid #D5D5D5;
        padding-left: calculateRem(48px);
        min-width: calculateRem(450px);

        &:before, &:after {
          content: '';
          background-color: #7C7C7C;
          @include size(calculateRem(20px));
          position: absolute;
          top: calculateRem(-10px);
          z-index: 1;
        }

        &:before {
          background-color: #FD7348;
        }

        &:after {
          right: -20px;
        }
      }


      h3 {
        font-weight: 400;
        font-size: calculateRem(56px);
        line-height: calculateRem(56px);
        letter-spacing: -0.04em;
        margin-bottom: calculateRem(12px);
        white-space: pre;
      }

      p {
        font-weight: 400;
        font-size: calculateRem(24px);
        line-height: calculateRem(32px);
        letter-spacing: -0.04em;
      }
    }
  }

  &__map {
    display: flex;
    align-items: center;
    height: 100%;

    rect {
      pointer-events: none;
    }

    .hovered {
      cursor: pointer;
      fill: white;
      @extend %transition;

      &:hover {
        fill: $yellow;
      }

    }

    &__title {
      position: absolute;
      opacity: 0;
      font-weight: 500;
      font-size: calculateRem(16px);
      line-height: calculateRem(24px);
      padding: calculateRem(12px) calculateRem(16px);
      background-color: $white;
      pointer-events: none;
      box-shadow: 0px 37px 15px rgba(0, 0, 0, 0.01), 0px 21px 13px rgba(0, 0, 0, 0.05), 0px 9px 9px rgba(0, 0, 0, 0.09), 0px 2px 5px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1);
      display: flex;
      align-items: center;
      transform: translateX(-50%);

      img {
        margin-right: calculateRem(8px);
        width: calculateRem(26px);
        height: calculateRem(20px);
        object-fit: contain;
      }

      &.show {
        opacity: 1;
      }
    }
  }

  @include media("<=laptop") {
    .section-title {
      margin-bottom: 110px;
    }
    .section-menu {
      li {
        a {
          font-size: calculateRem(88px);
          line-height: calculateRem(88px);
        }
      }
    }

    &__info {
      margin-top: calculateRem(80px);
    }
    &__map {
      margin-left: auto;
    }
  }

  @include media("<=wrapper") {
    &__info {
      &__item {
        h3 {
          font-size: 40px;
          line-height: 40px;
        }

        p {
          font-size: 20px;
          line-height: 28px;
        }
      }
    }

    &__map {
    }
  }

  @include media("<=tablet") {
    padding-top: 32px;

    .container-fluid {
      padding-left: 0 !important;
    }

    .section-title {
      margin-bottom: 24px;
      padding-left: 20px;
    }

    &__pattern {
      display: none;
    }

    .section-menu {
      padding-left: 20px;
      flex-direction: row;
      align-items: flex-start;

      li {
        a {
          font-size: 50px;
          line-height: 55px;
        }
      }
    }

    &__info {
      margin-bottom: 40px;

      &__item {
        &:first-child {
          min-width: calculateRem(300px);
        }

        h3 {
          font-weight: 500;
          font-size: 24px;
          line-height: 24px;
          letter-spacing: -0.04em;
          margin-bottom: 4px;
        }

        p {
          font-size: 12px;
          line-height: 20px;
          letter-spacing: -0.02em;
        }
      }
    }

    &__map {
      padding-left: 20px;
    }
  }

  @include media("<=phone") {

    &__info {
      width: auto;
      padding-left: 20px;
      margin: 40px -20px 40px 0;

      &__item {
        min-width: auto !important;
        border-top: 1px solid #D5D5D5;
        padding-top: 26px;
        width: 100%;

        &:first-child {
          padding-left: 0;

          &:before, &:after {
            @include size(12px);
            top: -6px;
          }
        }

        h3 {
          font-weight: 500;
          font-size: 24px;
          line-height: 24px;
          letter-spacing: -0.04em;
          margin-bottom: 4px;
        }

        p {
          font-size: 12px;
          line-height: 20px;
          letter-spacing: -0.02em;
        }
      }
    }

    &__map {
      width: 100%;
      padding-left: 20px;
    }

    .section-menu {
      li {
        a {
          font-size: 24px;
          line-height: 24px;
          letter-spacing: -0.04em;
        }
      }
    }
  }
}