.services-page {
  margin-top: calculateRem(100px);

  &__item {
    margin-top: calculateRem(60px);
    padding-top: calculateRem(130px);
    background-image: url("../images/services-page-pattern.svg");
    background-repeat: no-repeat;
    background-position: top center;
  }

  &__desc {
    padding: calculateRem(100px);
    background-color: #F9F9F9;

    span {
      font-weight: 400;
      font-size: calculateRem(24px);
      line-height: calculateRem(32px);
      letter-spacing: -0.01em;
      color: #7C7C7C;
      margin-bottom: calculateRem(4px);
      display: block;
    }

    h3 {
      font-weight: 600;
      font-size: calculateRem(32px);
      line-height: calculateRem(40px);
      letter-spacing: -0.01em;
      margin-bottom: calculateRem(40px);
    }

    p {
      font-weight: 400;
      font-size: calculateRem(16px);
      line-height: calculateRem(26px);
      margin-bottom: calculateRem(24px);
    }
  }

  &__text {
    max-width: calculateRem(400px);
  }

  &__img {
    img {
      width: 100%;
    }
  }

  .partners {
    overflow: unset;

    &:before {
      display: none;
    }

    .section-title {
      color: $black;
    }

    &__btn {
      display: none;
    }

    &__block {
      margin-right: 0;
    }

    &__row {
      li {
        &:hover {
          box-shadow: 1px 0 0 0 $black,
          0 1px 0 0 $black,
          1px 1px 0 0 $black,
          1px 0 0 0 $black inset,
          0 1px 0 0 $black inset;
        }

        a {

          img {
            filter: brightness(0) saturate(100%) invert(50%) sepia(9%) saturate(0%) hue-rotate(135deg) brightness(96%) contrast(94%);
          }
        }
      }
    }
  }

  .contact-section {
    margin-top: 0;

    &__text {
      padding-top: 0;

      span {
        font-weight: 500;
        font-size: calculateRem(16px);
        line-height: calculateRem(26px);
        letter-spacing: -0.04em;
        display: block;
        padding: calculateRem(6px) calculateRem(18px);
        border: 1px solid $white;
        border-radius: calculateRem(100px);
        width: max-content;
        margin: 0 auto calculateRem(12px);
      }
    }
  }

  @include media("<=wrapper") {
    .partners {
      &__item {
        img {
          width: 120px;
        }
      }
    }
  }

  @include media("<=tablet") {
    margin-top: 0;
    &__item {
      background-image: unset;
      padding-top: 0;
      margin-top: 32px;
    }

    &__desc {
      padding: 0;
      background-color: $white;
      margin-right: 32px;
    }

    &__text {
      max-width: 100%;

      span {
        font-size: 13px;
        line-height: 20px;
        letter-spacing: -0.01em;
        margin-bottom: 0;
      }

      h3 {
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.01em;
        margin-bottom: 20px;
      }

      .btn {
        margin: 40px -20px 0;
        width: auto;
        display: flex;
      }
    }

    .partners {
      margin-top: 40px;
      padding: 0;

      .section-title {
        margin-bottom: 32px;
      }

      &__btn {
        display: block;
        margin: 40px -20px 0;
        text-align: center;
      }
    }
  }

  @include media("<=phone") {
    margin-top: 0;
    &__item {
      flex-direction: column-reverse;
    }

    &__img {
      margin-bottom: 32px;
    }

    &__desc {
      margin-right: 0;
    }
  }
}